<template>
    <div class="container">
        <v-card>
            <v-list>
                <v-list-tile v-for="user in users" :key="item" avatar @click="">
                    <v-list-tile-content>
                        <v-list-tile-title v-text="user"></v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
            </v-list>
        </v-card>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "Home",
        data() {
            return {
                active: null,
                users: [],
                forms: null,
                text: 'test'
            }
        }, methods: {
            next() {
                const active = parseInt(this.active)
                this.active = (active < 2 ? active + 1 : 0).toString()
            }
        },
        created() {
            axios.get('/GetUsers')
                .then(data => {
                    this.users = data.data;
                    console.log(data.data)
                }).catch(error => {
                console.log(error);
            });
            // axios.get('/GetTopForms')
            //     .then(data => {
            //         this.users = data.data;
            //     }).catch(error => {
            //     console.log(error);
            // })
        }
    }
</script>

